* {
  box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
}
/* Prevent 3px space at bottom of image: http://stackoverflow.com/questions/10844205/ */
img {
  vertical-align: middle;
}

html {
  /* For rem sizing. 1.0rem == 10px */
  font-size: 10px;
}
body {
  font-family: 'PT Sans', sans-serif;
  font-size: 1.6rem;
}

/* Colors */
.articleBarWrap {
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  width: 100%;
  font-family: 'Lora', serif;
  font-weight: bold;
  color: white;
  background-color: black;
}
.articleBarWrap .articleBar {
  overflow: hidden;
  width: 9999px;
}
.articleBarWrap .articleBar .twoWrap {
  width: 256px;
  float: left;
}
.articleBarWrap .articleBar .article {
  background-size: cover;
  width: 256px;
  height: 216px;
  float: left;
  position: relative;
  overflow: hidden;
  border-bottom: 0px solid #289f6d;
  opacity: 0.6;
}
.articleBarWrap .articleBar .article .image {
  background-size: cover;
  width: 100%;
  height: 100%;
}
.articleBarWrap .articleBar .article h4 {
  font-size: 1.8rem;
  text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  padding: 20px;
  margin: 0;
  text-align: center;
  position: absolute;
  width: 100%;
  bottom: 0;
}
.articleBarWrap .articleBar .article,
.articleBarWrap .articleBar .article > * {
  transition: 0.2s all;
}
.articleBarWrap .articleBar .article.active {
  opacity: 1;
}
.articleBarWrap .articleBar .article.active .image {
  transform: scale(1.1);
}
.articleBarWrap .articleBar .article.active h4 {
  transform: scale(1.3);
}

/* Colors */
.articleContent {
  margin-bottom: 60vh;
  transform: translate3d(0, 0, 0);
  font-family: "PT Sans", sans-serif;
  font-size: 1.85rem;
  padding: 0 60px 60px 60px;
  overflow: auto;
  /* Stuff that gets hidden */
  /* infobox */
  /* Thumbs & galleries*/
}
.articleContent p {
  line-height: 3.0rem;
}
.articleContent a {
  color: inherit;
  text-decoration: none;
}
.articleContent .hatnote,
.articleContent .mw-editsection,
.articleContent .noprint,
.articleContent .interProjectTemplate,
.articleContent .beginnetje {
  display: none;
}
.articleContent table.editmode {
  display: inline !important;
  float: none;
  clear: none;
}
.articleContent table.editmode > tbody,
.articleContent table.editmode > tbody > tr,
.articleContent table.editmode > tbody > tr > td,
.articleContent table.editmode > tbody > tr > th {
  display: inline !important;
}
.articleContent .infobox {
  background: #f4f4f4;
  border: 2px solid #289f6d;
  border-width: 2px 0;
  float: right;
  width: 360px !important;
  margin-right: -60px;
  font-size: 1.2rem !important;
  line-height: 1.7rem;
  font-weight: bold;
  border-spacing: 0;
  margin: 20px -60px 20px 20px !important;
}
.articleContent .infobox td,
.articleContent .infobox th {
  padding: 5px 20px;
}
.articleContent .infobox th.header,
.articleContent .infobox td.header,
.articleContent .infobox caption {
  font-size: 1.7rem;
  line-height: 2.1rem;
  background-color: transparent !important;
}
.articleContent .infobox td.subscript {
  text-align: center;
  font-weight: normal;
  font-size: 1.2rem !important;
  line-height: 1.8rem;
}
.articleContent .infobox img.fullWidth {
  width: 360px;
  height: auto;
  margin: 0 -20px;
}
.articleContent .infobox ul {
  margin-top: 10px;
  margin-bottom: 10px;
}
.articleContent .tright {
  float: right;
  clear: right;
}
.articleContent .tleft {
  float: left;
  clear: left;
}
.articleContent > .tright,
.articleContent > .tleft {
  float: right;
  clear: right;
  margin: 10px -60px 10px 20px;
}
.articleContent .thumb {
  background: #f4f4f4;
  border: 2px solid #289f6d;
  border-width: 2px 0;
}
.articleContent .thumb a {
  display: inline;
  margin: 0;
  padding: 0;
}
.articleContent .thumb img {
  margin: 0;
  padding: 0;
}
.articleContent .thumb .thumbcaption {
  text-align: center;
  font-weight: normal;
  font-size: 1.2rem !important;
  line-height: 1.8rem;
  padding: 5px;
}
.articleContent .thumb.fullScreen {
  width: calc(100% +  60px  +  60px);
  margin: 10px -60px;
  float: none;
  clear: none;
}
.articleContent .thumb.fullScreen > div {
  width: 100% !important;
}
.articleContent .thumb.fullScreen img {
  width: 100%;
  height: auto;
}
.articleContent .gallery.mw-gallery-traditional {
  list-style: none;
  margin: 0;
  padding: 0;
}
.articleContent .gallery.mw-gallery-traditional .gallerycaption {
  display: block;
  text-align: center;
  font-weight: normal;
  font-size: 1.2rem !important;
  line-height: 1.8rem;
  font-weight: bold;
  margin: 10px;
}
.articleContent .gallery.mw-gallery-traditional li {
  display: inline-block;
  vertical-align: top;
}
.articleContent .gallery.mw-gallery-traditional li > div {
  text-align: center;
}
.articleContent .gallery.mw-gallery-traditional .gallerytext,
.articleContent .gallery.mw-gallery-traditional .gallerytext p {
  text-align: center;
  font-weight: normal;
  font-size: 1.2rem !important;
  line-height: 1.8rem;
}

/* Colors */
.articleContent .articleHeader {
  margin: 0 -60px;
  position: relative;
  min-height: 400px;
  max-height: 400px;
  overflow: hidden;
  background-color: black;
}
.articleContent .articleHeader img {
  width: 100%;
  vertical-align: bottom;
  display: inline-block;
}
.articleContent .articleHeader h1 {
  position: absolute;
  margin-left: 60px;
  margin-right: 60px;
  bottom: 0;
  color: white;
  text-shadow: 0 0 6px rgba(0, 0, 0, 0.5);
  font-family: 'Lora', serif;
  font-size: 4.0rem;
}
.articleContent .articleHeader button.showFullscreen {
  position: absolute;
  right: 10px;
  bottom: 10px;
  width: 38px;
  height: 38px;
  background: url('../img/fullscreen.svg');
  background-size: 100% 100%;
  border: 0;
}
.imageOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.8);
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  display: none;
}
.imageOverlay .close {
  position: absolute;
  top: 0;
  right: 0;
}
.imageOverlay .spinner {
  margin-top: 40vh;
}

/* Colors */

.spinner {
  width: 40px;
  height: 40px;
  position: relative;
  margin: 20px auto;
}
.spinner:before,
.spinner:after {
  content: ' ';
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: white;
  opacity: 0.8;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: bounce 2s infinite ease-in-out;
  animation: bounce 2s infinite ease-in-out;
}
.spinner.dark:before,
.spinner.dark:after {
  background-color: black;
}
.spinner:after {
  -webkit-animation-delay: -1s;
  animation-delay: -1s;
}
@-webkit-keyframes bounce {
  0%,
  100% {
    -webkit-transform: scale(0);
  }
  50% {
    -webkit-transform: scale(1);
  }
}
@keyframes bounce {
  0%,
  100% {
    transform: scale(0);
    -webkit-transform: scale(0);
  }
  50% {
    transform: scale(1);
    -webkit-transform: scale(1);
  }
}

/* Colors */
.tocMenu {
  position: fixed;
  top: 40px;
  width: 380px;
  left: -380px;
  bottom: 0px;
  background: black;
  color: white;
  padding-left: 50px;
  overflow: auto;
  background-size: 100% 100%, 100% auto;
  background-repeat: repeat, no-repeat;
  background-position: left top, center top;
  transform: translate3d(0, 0, 0);
}
.tocMenu #toctitle {
  font-family: 'Lora', serif;
  text-transform: uppercase;
  color: #c1c1c1;
  letter-spacing: 0.3rem;
  margin: 10px 0;
}
.tocMenu #toctitle h2 {
  font-size: 1.35rem;
  margin: 0;
}
.tocMenu h2.articleTitle {
  font-family: 'Lora', serif;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  margin: 10px 40px 10px 0;
  margin-right: 40px;
  font-size: 2.2rem;
  line-height: 3.5rem;
}
.tocMenu #toc > ul {
  padding-left: 0;
}
.tocMenu ul {
  list-style: none;
  margin: 0;
  padding: 0 0 0 1.8rem;
}
.tocMenu ul a {
  color: white;
  font-size: 1.8rem;
  font-weight: bold;
  text-decoration: none;
  padding: 5px 0;
  display: block;
  border-right: 4px solid transparent;
}
.tocMenu ul a.active {
  color: #289f6d;
  border-right-color: #289f6d;
}
.tocMenu ul ul a {
  font-size: 1.6rem;
}
.tocMenu ul ul ul a {
  font-size: 1.4rem;
}

/* Colors */
body {
  padding-top: 40px;
}
.topBar {
  height: 40px;
  position: fixed;
  top: 0;
  width: 100%;
  background: white;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  perspective: 800px;
  perspective-origin: 150px 50%;
}
.topBar .carousel {
  width: 300px;
  height: 40px;
  position: relative;
  transform-style: preserve-3d;
  transform-origin: 50% 50% 0;
  transform: translateZ(-20px);
}
.topBar .carousel .side {
  position: absolute;
  width: 300px;
  height: 40px;
  backface-visibility: hidden;
}
.topBar .carousel .side.front {
  transform: translateZ(20px);
}
.topBar .carousel .side.top {
  transform: translateY(-20px) rotateX(90deg);
}
.topBar .carousel .side.bottom {
  transform: translateY(20px) rotateX(-90deg);
}
.topBar .carousel .side.back {
  transform: translateZ(-20px) rotateX(-180deg);
}
.topBar .carousel .side img {
  vertical-align: top;
}
.topBar .carousel .side .header {
  color: #289f6d;
  line-height: 40px;
  padding-left: 50px;
  font-size: 18px;
  font-weight: bold;
  display: block;
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.topBar .menuButton {
  width: 40px;
  height: 40px;
  position: absolute;
  transform-origin: 50% 50% -20px;
  backface-visibility: hidden;
  top: 0;
  background: black center center no-repeat url('../img/menu.svg');
  background-size: 30px auto;
  cursor: pointer;
}
.topBar .textSizeButton {
  width: 40px;
  height: 40px;
  float: right;
  background: black center center no-repeat url('../img/text_size.svg');
}
.topBar .textSizeOverlay {
  position: fixed;
  top: 40px;
  right: 0;
  background: black;
  color: white;
  width: 300px;
  padding-bottom: 40px;
}
.topBar .textSizeOverlay .close {
  position: absolute;
  top: 0;
  right: 0;
}
.topBar .textSizeOverlay h5 {
  color: #c0c0c0;
  font-family: "Lora", sans-serif;
  font-size: 13px;
  font-weight: bold;
  text-transform: uppercase;
  text-align: center;
  letter-spacing: 5px;
  margin: 10px;
}
.topBar .textSizeOverlay .size {
  border-left: 5px solid transparent;
  line-height: 60px;
  font-family: "PT Sans", sans-serif;
  font-weight: bold;
}
.topBar .textSizeOverlay .size.active {
  border-color: #289f6d;
}
.topBar .textSizeOverlay .size .letter {
  padding: 20px;
}
.topBar .textSizeOverlay .size.normal .letter {
  font-size: 20px;
}
.topBar .textSizeOverlay .size.normal .label {
  font-size: 16px;
}
.topBar .textSizeOverlay .size.medium .letter {
  font-size: 25px;
}
.topBar .textSizeOverlay .size.medium .label {
  font-size: 21px;
}
.topBar .textSizeOverlay .size.large .letter {
  font-size: 30px;
}
.topBar .textSizeOverlay .size.large .label {
  font-size: 26px;
}
.topBar .textSizeOverlay .size .label {
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  display: inline-block;
  min-width: 200px;
}
